import React, { useState } from 'react';
import { useContract } from './useContract';
import CryptoJS from 'crypto-js';
import 'bulma/css/bulma.min.css';
const { ethers, JsonRpcProvider, Web3Provider } = require('ethers');


function App() {
  const [file, setFile] = useState(null);
  const [hash, setHash] = useState('');
  const [hashToVerify, setHashToVerify] = useState('');
  const [verificationResult, setVerificationResult] = useState(null);
  const contract = useContract();

  const onFileChange = (event) => {
    const file = event.target.files[0];
    if (file) {
      const reader = new FileReader();
      reader.onload = function(e) {
        const binary = e.target.result;
        const hash = CryptoJS.SHA256(binary).toString(CryptoJS.enc.Hex);
        setHash(hash);
      };
      reader.readAsBinaryString(file);
    }
  };

  const recordHash = async () => {
    if (!hash.trim()) {
      alert('Please upload a file to generate a hash.');
      return;
    }
    try {
      const tx = await contract.addHash(hash);
      await tx.wait();
      alert('Hash recorded successfully!');
    } catch (error) {
      alert(`Failed to record hash: ${error.message}`);
    }
  };

  const verifyHash = async () => {
    if (!hashToVerify.trim()) {
      alert('Please enter a hash to verify.');
      return;
    }
    try {
      const result = await contract.findHash(hashToVerify);
      setVerificationResult(result);
    } catch (error) {
      alert(`Failed to verify hash: ${error.message}`);
    }
  };

  return (
    <div className="container">
      <h1 className="title">Image Hash Recorder</h1>
      <div className="file has-name">
        <label className="file-label">
          <input className="file-input" type="file" onChange={onFileChange} />
          <span className="file-cta">
            <span className="file-icon">
              <i className="fas fa-upload"></i>
            </span>
            <span className="file-label">
              Choose a file
            </span>
          </span>
          {file && <span className="file-name">
            {file.name}
          </span>}
        </label>
      </div>
      <button className="button is-primary" onClick={recordHash}>Record Hash of Uploaded Image</button>
      <p>Hash: {hash}</p>

      <h1 className="title">Verify Image Hash</h1>
      <div className="field">
        <div className="control">
          <input className="input" type="text" value={hashToVerify} onChange={(e) => setHashToVerify(e.target.value)} placeholder="Enter hash to verify" />
        </div>
      </div>
      <button className="button is-link" onClick={verifyHash}>Verify Hash</button>
      {verificationResult !== null && (
        <p>Verification Result: {verificationResult ? "Exists" : "Does not exist"}</p>
      )}
    </div>
  );
}

export default App;